.form {
    width: 310px;
    height: 140px;
    background-color: #8aedb5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 10px;

}

.icon {
    height: 200px;
    width: 250px;
    background-color: #91abde;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.inputField {
    width: 300px;
}

.button {
    height: 40px;
}

@media(max-width: 450px){
    .form {
        width: 250px;
        height: 140px;
        background-color: #8aedb5;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
    }

    .inputField {
        width: 100%;
    }
}